:root {
    /* dark mood */
    --primary-color: #f4f5f1;
    --secondary-color: #1bc3b8;
    --dark-color: #0c111d;
    --light-dark-color: #1d2531;
    --form-color:#2a3341;
    --blue-color: #0003bd;
    --peach-color: #c90468;
}

.contact-form-section{
    padding: 10px;
    display: flex;
     justify-content: center;
     align-items: center;
     flex-direction: column;
     width: 100%;
}

.contact-section{
    display: flex;
    /* flex-direction: column; */
    text-align: center;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;

}
form{
    width: 100%;
}

.form-header h2{
    color: var(--secondary-color);
    font-size: 60px;
    font-weight: bold;
}


.form-header p{
    color: var(--primary-color);
    font-weight: bolder;
}

.contact-form {
    /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
    width: 70%;
    margin-top: 20px;
}
.contact-form form input::placeholder{
    text-transform: capitalize;
    padding-left:10px ;    
}
.contact-form form input{
    width: 100%;
    color: var(--primary-color);
    opacity: 0.9;
    background: var(--form-color);
    border:none;
    height: 35px;
    padding: 10px;
    margin-bottom: 15px;
}
.contact-form form input:focus , .contact-form form textarea:focus{
    outline: none;
}
.contact-form form textarea{
    width: 100%;
    resize: none;
    height: 15rem;
    color: var(--primary-color);
    opacity: 0.9;
    padding: 10px;
    border:none;
    background-color: var(--form-color);
  font-family: 'Open Sans', sans-serif;
}
.map{
    width: 100%;
    overflow: hidden;
}

.leaflet-container{
    width: 100;
    color: var(--primary-color);
    opacity: 0.4;
    background: var(--light-dark-color);
    height: 100vh;
    background-color: var(--secondary-color);
    /* color: blue !important; */
}

.submit{
    display: flex;
    justify-content: left;
    margin-top: 20px;
}
.submit button{
    border: 1px solid var(--secondary-color);
    padding:10px 30px;
    background-color: transparent;
    color:var(--secondary-color);
    transition: .3s;
}

.submit button:hover{
    color: var(--dark-color);
    background-color: var(--secondary-color);
    transition: .3s;
}
.email,.name,.message{
    display: flex;
    flex-direction: column;
}

.email,.name,.message{
    text-align: left;
}
.contact-form  label{
    color: var(--primary-color);
    margin-bottom: 6px;
}